*{
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.hero {
  text-align: center;
  background-color: #e6f0ff;
  padding: 4rem 2rem;
}

.hero h1 {
  font-size: 1.5rem;
  color: #003399;
}

.hero p {
  font-size: 1.2rem;
  color: #222;
  margin: 1rem 0;
}

.hero button {
  background-color: #003399;
  color: white;
  padding: 0.75rem 1.5rem;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 5px;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  padding: 2rem;
  background-color: #fff;
  margin: auto;
  width: 80%;
}

.card {
  display: flex;
  width: calc(50% - 4rem);
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.05);
  padding: 1.5rem;
  align-items: center;
  gap: 1.5rem;
  min-width: 300px;
}

.icon img {
  width: 100px;
  height: 100px;
}

.info h3 {
  font-size: 1.2rem;
  color: #05173b;
  margin-bottom: 0.5rem;
}

.info p {
  font-size: 0.95rem;
  color: #555;
  margin-bottom: 1rem;
}

.quote-btn {
  background: transparent;
  border: 2px solid;
  padding: 0.5rem 1.2rem;
  border-radius: 50px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}

.quote-btn.orange {
  color: #f28c00;
  border-color: #f28c00;
}

.quote-btn.orange:hover {
  background: #f28c00;
  color: white;
}

.quote-btn.pink {
  color: #d12c74;
  border-color: #d12c74;
}

.quote-btn.pink:hover {
  background: #d12c74;
  color: white;
}

.quote-btn.green {
  color: #1ca969;
  border-color: #1ca969;
}

.quote-btn.green:hover {
  background: #1ca969;
  color: white;
}

.quote-btn.blue {
  color: #2a78d1;
  border-color: #2a78d1;
}

.quote-btn.blue:hover {
  background: #2a78d1;
  color: white;
}

@media (max-width: 768px) {
  .card {
    width: 100%;
  }
}


.hero-banner {
  background-color: rgba(63, 169, 245, 0.2);
  padding: 1rem 2rem;
}

.hero-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.hero-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 1rem;
  font-weight: 500;
}

.hero-item i {
  color: #20ade3;
  font-size: 1.2rem;
}
.star-rating {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.stars {
  display: flex;
  gap: 2px;
}

.star {
  position: relative;
  display: inline-block;
  font-size: 20px;
  width: 20px;
  height: 20px;
}

.star .fa-star {
  position: absolute;
  top: 0;
  left: 0;
}

.star .far.fa-star {
  color: white; /* empty star outline */
  z-index: 1;
}

.star .fas.fa-star.star-fill {
  color: green;
  overflow: hidden;
  white-space: nowrap;
  z-index: 2;
}
.hero-banner-contect {
  background: linear-gradient(90deg, #030722 0%, #02092f 100%);
  color: white;
  padding: 4rem 2rem;
}

.hero-content-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: 0 auto;
  gap: 2rem;
}

.hero-text {
  flex: 1 1 500px;
}

.section-label {
  color: #33aaff;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

.hero-text h1 {
  font-size: 2.5rem;
  font-weight: bold;
  line-height: 1.3;
  margin-bottom: 1.5rem;
}

.hero-text .highlight {
  color: #33aaff;
}

.hero-btn {
  background-color: #33aaff;
  color: black;
  padding: 0.9rem 2rem;
  border-radius: 50px;
  font-weight: bold;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  margin-top: 1.5rem;
  transition: background 0.3s ease;
}

.hero-btn:hover {
  background-color: #e69400;
}

.stats {
  display: flex;
  gap: 2rem;
  margin-top: 3rem;
  font-size: 1rem;
}

.stats > div {
  border-left: 1px solid #999;
  padding-left: 1rem;
}

.stats > div:first-child {
  border: none;
  padding-left: 0;
}

.hero-image {
  flex: 1 1 400px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.blob {
  position: relative;
  width: 100%;
  max-width: 400px;
  aspect-ratio: 1 / 1;
  border-radius: 50% 50% 0 50% / 60% 60% 40% 40%;
  overflow: hidden;
  background-color: #fff;
}

.blob img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.section-alt {
  padding: 50px 0;
}

.container {
  width: 80%;
  margin: 0 auto;
}

.row.alt {
  display: flex;
  align-items: center;
  margin-bottom: 80px;
  gap: 40px;
  flex-wrap: wrap;
}

.row.alt.reverse {
  flex-direction: row-reverse;
}

.image-container {
  flex: 1;
  max-width: 50%;
}

.custom-img {
  width: 100%;
  clip-path: path("M0,100 C0,0 100,0 100,100 C100,100 0,100 0,100 Z");
  border: 5px solid #3ca3f2;
  border-radius: 40% 60% 60% 40% / 50% 50% 50% 50%;
  object-fit: cover;
}

.text-content {
  flex: 1;
  max-width: 50%;
}

.text-content h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

.text-content p {
  font-size: 16px;
  line-height: 1.6;
}

/* Responsive layout */
@media (max-width: 768px) {
  .row.alt, .row.alt.reverse {
    flex-direction: column;
  }

  .image-container, .text-content {
    max-width: 100%;
  }

  .custom-img {
    clip-path: none;
    border-radius: 20px;
  }
}

.alt-sections {
  width: 80%;
  margin: 0 auto;
  padding: 60px 0;
}

.alt-section {
  display: flex;
  align-items: center;
  gap: 40px;
  margin-bottom: 80px;
  flex-wrap: wrap;
}

.alt-section.reverse {
  flex-direction: row-reverse;
}

.alt-image {
  flex: 1;
  max-width: 50%;
  position: relative;
}

.alt-image img {
  width: 100%;
  height: auto;
  object-fit: cover;
  clip-path: ellipse(70% 50% at 50% 50%);
  border-radius: 20px;
  box-shadow: 0 0 20px rgba(0,0,0,0.1);
  border: 4px solid #3ca3f2;
}

.alt-text {
  flex: 1;
  max-width: 50%;
}

.alt-text h2 {
  font-size: 26px;
  margin-bottom: 20px;
  color: #333;
}

.alt-text p {
  font-size: 16px;
  line-height: 1.6;
  color: #555;
}

/* Responsive */
@media (max-width: 768px) {
  .alt-section, .alt-section.reverse {
    flex-direction: column;
    text-align: center;
  }

  .alt-image,
  .alt-text {
    max-width: 100%;
  }

  .alt-image img {
    clip-path: none;
    border-radius: 15px;
  }
}
.card-slider-section{
  width: 80%;
  margin: auto;
}
.brand-slider-section {
  width: 80%;
  margin: auto;
  padding: 1rem;
}
.explain-section {
      background-image: linear-gradient(-180deg, #fff 5%, #f4f4f2 0);
}
.Exp-image {
  flex: 1;
  max-width: 50%;
  position: relative;
}

.Exp-image  img {
  width: 100%;
  height: auto;
  object-fit: cover;
  border-radius: 20px;
  box-shadow: 0 0 20px rgba(0,0,0,0.1);
  border: 4px solid #3ca3f2;
}