.navbar {
  background-color: white;
  padding: 1rem 2rem;
  border-bottom: 1px solid #ccc;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.logo {
  font-size: 1.5rem;
  color: #20ade3;
  text-decoration: none;
  font-weight: bold;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 2rem;
  align-items: center;
  padding: 0;
  margin: 0;
}

.nav-links a,
.dropbtn {
  color: #20ade3;
  text-decoration: none;
  cursor: pointer;
  font-weight: 500;
  font-size: 1.1rem;
  position: relative;
}

/* Dropdown */
.dropdown {
  position: relative;
}

.dropdown-content {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: white;
  display: none;
  flex-direction: column;
  min-width: 200px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
  z-index: 10;
}

.dropdown-content a {
  color: #20ade3;
  padding: 0.75rem 1rem;
  text-decoration: none;
  border-bottom: 1px solid #eaeaea;
}

.dropdown-content a:last-child {
  border-bottom: none;
}

.dropdown-content a:hover {
  background-color: #f0faff;
}

.dropdown.open .dropdown-content {
  display: flex;
}

/* Also show dropdown on hover */
.dropdown:hover .dropdown-content {
  display: flex;
}
.nav-links li:not(:last-child)::after {
  content: "|";
  color: #20ade3;
  margin-left: 1rem;
}
