.Band-Container {
  overflow: hidden;
  white-space: nowrap;
  width: 100%;
  box-sizing: border-box;
  height: 52px;
  background: white;
}

.brand-slider {
  display: flex;
  animation: scroll 20s linear infinite;
}

.brand-slider .slide {
  flex: 0 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
}

.brand-slider .slide img {
  max-width: 100%;
  height: auto;
}

@keyframes scroll {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-33.33%);
  }
}
